.sticker-container {
    width: 100%;
    display: flex;
    flex-direction: row;
    padding-left: 2vw;
    padding-top: 2vw;
    /* justify-content: space-between; */
    /* background-color: aliceblue; */
}

.left-display {
    display: flex;
    flex-direction: column;
    /* background-color: aliceblue; */
    /* height: 25vw; */
    /* margin-left: 2vw;
    margin-right: 5vw; */
    justify-content: space-between;
    /* padding-bottom: 0.25vw; */
    /* padding-left: 1vw; */
}

.right-display {
    display: flex;
    flex-direction: column;
    padding-left: 0.25vw;
    /* height: 15vw;
    width: 73vw;
    margin-left: 2vw;
    margin-right: 5vw; */
