.text-box {
    width: 30vw;
    padding: 1.5vw;
    height: 100%;
    display: flex;
    flex-direction: column;
    /* justify-content: space-between; */
}

.fill {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    width: 40vw;
    height: 25vw;
}

.fill-fifth {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    width: 20vw;
    height: 15vw;
    /* padding-left: 0.25vw; */
}

.fill img {
    flex-shrink: 0;
    min-width: 100%;
    min-height: 100%;
    object-fit: cover;
}

.fill-fifth img {
    flex-shrink: 0;
    min-width: 100%;
    min-height: 100%;
    object-fit: cover;
}


.smaller-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-top: 0.25vw;
}

.sticker-title {
    padding-top: 3vw;
    font-size: 3vw;
    font-family: 'Lexend Deca', sans-serif;
    font-weight: 500;
    width: 80vw;
    color: #2D3739;
}

.sticker-bible {
    font-size: 0.8vw;
    font-family: 'Tenor Sans', sans-serif;
    width: 27vw;
    padding-top: 2vw;
    color: #394648;
}

.sticker-description {
    font-size: 0.9vw;
    font-family: 'Lexend Deca', sans-serif;
    font-weight: 500;
    width:27vw;
    padding-top: 2vw;
    color: #5C6A6F;
}

.bottom-one {
    padding-top: 2vw;
}

.shop-text {
font-family: 'Tenor Sans', sans-serif;
font-size: 0.6vw;
transition: font-size 0.25s;
}

.shop-text:hover {
font-size: 0.7vw;
}

.button-format {
/* padding-left: 7vw; */
padding-top: 2vw;
}

