body {
  margin: 0;
  font-family: 'Tenor Sans', 'Montserrat', 'Lexend Deca', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    text-align: center;
  }
  
  h1 {
    font-family: 'meno-banner', serif;
    font-size: 3.5vw;
    margin: 0px 0px;
    font-weight: 450;
  }
  
  h2 {
    font-family: 'meno-banner', serif;
    font-size: 2.5vw;
    margin: 0px 0px;
    font-weight: 200;
  }
  
  h3 {
    font-family: 'Inter', sans-serif;
    font-size: 1.25vw;
    font-style: normal;
    font-weight: 400;
    line-height: 200%;
  }
  
  h4 {
    font-family: 'Inter', sans-serif;
    font-size: 1.25vw;
    font-style: normal;
    font-weight: 400;
    line-height: 200%;
    margin: 0;
    letter-spacing: 0.15vw;
  }
  
  h5 {
    font-family: 'Inter', sans-serif;
    font-size: 0.75vw;
    font-style: normal;
    font-weight: 400;
    margin: 0;
  }
  
  h6 {
    font-family: 'Inter', sans-serif;
    font-size: 0.8vw;
    font-style: normal;
    font-weight: 400;
    margin: 0.75vw 0 0.75vw;
  }
  
  a {
    font-family: 'Inter', sans-serif;
    color: #1f1f1f;
  }
  
  input {
    font-family: 'Inter', sans-serif;
  }
  
  .fade-enter {
    opacity: 0.01;
  }
  
  .fade-enter.fade-enter-active {
    opacity: 1;
    transition: opacity 300ms ease-in;
  }
  
  .fade-exit {
    opacity: 1;
  }
  
  .fade-exit.fade-exit-active {
    opacity: 0.01;
    transition: opacity 300ms ease-in;
  }
  
  .transition-group {
    position: relative;
  }
  
  section.route-section {
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
  }
  
  @media (max-width: 480px) {
    h1 {
      font-size: 32px;
    }
  
    h2 {
      font-size: 24px;
    }
  
    h3 {
      font-size: 12px;
      line-height: 200%;
    }
  }
  
.navbar-container {
    position: absolute;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    z-index: 100;
    padding-bottom: 2vw;
  }
  
  .logo,
  .navlinks {
    padding-top: 2vw;
  }
  
  .logo {
    width: 7vw;
    margin-top: 1vw;
    margin-left: 7.5vw;
  }

  .logo-name {
    font-family: 'Lexend Deca', sans-serif;
    font-size: 1vw;
    padding-bottom: 0.3vw;
    padding-left: 5vw;
  }
  
  .navlinks {
    position: absolute;
    right: 0;
    width: 53vw;
    margin-right: 7.5vw;
  }
  
  ul {
    width: 100%;
    font-family: 'Lexend Deca', sans-serif;
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    padding: 0;
    margin-top: 1vw;
  }
  
  li {
    font-size: 1vw;
    padding-bottom: 0.3vw;
    padding-left: 5vw;
  }
  
  a {
    text-decoration: none;
  }
  
  li > a:after {
    background: none repeat scroll 0 0 transparent;
    content: '';
    display: block;
    height: 0.05vw;
    left: 50%;
    position: relative;
    top: 0.5vw;
    background: #1f1f1f;
    transition: width 0.3s ease 0s, left 0.3s ease;
    width: 0;
  }
  
  li > a:hover:after {
    width: 100%;
    left: 0;
  }
  
  #logo-image {
    width: 100%;
  }
  
  .navlink {
    color: #1f1f1f;
    letter-spacing: 0.1vw;
    font-family: 'Lexend Deca', sans-serif;
  }
  
  .navlink:hover {
    cursor: pointer;
  }
  
  .navlink {
    color: #5C6A6F;
  }
  
.footer-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    position: relative;
    height: 5%;
  }
  
  .footer-blurb {
    width: 30vw;
    padding: 1.5vw 1vw 1.5vw;
    margin-left: 1.5vw;
  }

  .spero-berkeley {
    width: 30vw;
    padding: 1.5vw 1vw 1.5vw;
    margin-left: 1.5vw;
  }
  
  .footer-social-container {
    display: flex;
    flex-direction: column;
    width: 10vw;
    padding: 1vw;
    margin-right: 1.5vw;
    z-index: 2;
  }
  
  .social-icons-container {
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: row;
    height: 2vw;
    justify-content: space-around;
    margin-bottom: 0.5vw;
  }
  
  .icon {
    width: 1.5vw;
  }
  
  a {
    width: 100%;
    height: 2.2vw;
  }
  
  img {
    width: 100%;
  }
  
  .bottom-border {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 35vw;
    height: 13.414vw;
    z-index: 1;
  }
  
  .bottom-border > img {
    height: 100%;
  }
  
  @media (max-width: 768px) {
    .footer-container {
      display: none;
    }
  }
  
